* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  max-width: 1920px;
  width: 100%;
}

ul li {
  list-style: none;
}

.container {
  max-width: 1665px;
  width: 100%;
  margin: 0 auto;
}

.btn {
  cursor: pointer;
}

.btn-list {
  width: 128px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: #0090F8;
  -webkit-border-radius: 16px;
  -moz-border-radius: 16px;
  border-radius: 16px;
  font-size: 18px;
  font-weight: 700;
  color: #FFFFFF;
  margin-right: 15px;
}

.header {
  background-color: #E4E4E4;

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0 15px 255px;
  }

  .menu__list {
    display: flex;

    &-item {
      margin-right: 17px;
    }

    &-link {
      text-decoration: none;
      color: #181818;
      font-size: 18px;
      line-height: 27px;
      transition: all .2s ease-in-out;
    }

    &-link:hover {
      color: #808080;
      border-bottom: 1px solid #808080;
    }
  }

  .search {
    position: relative;
    display: flex;
    flex-wrap: nowrap;

    &__input {
      width: 255px;
      outline: none;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      border-radius: 8px;
      border: none;
      box-shadow: 0 8px 16px #C8C8C8;
      padding-left: 65px;
      font-size: 18px;
      line-height: 27px;
      color: #808080;

      &::placeholder {
        font-size: 18px;
        line-height: 27px;
        color: #808080;
      }
    }

    &__img {
      position: absolute;
      width: 26px;
      height: 26px;
      left: 165px;
      top: 20px;
    }
  }
}

.hot-collection {
  padding-top: 50px;

  &__title {
    font-size: 64px;
    line-height: 96px;
    color: #000000;
    font-weight: 400;
  }
}

.collection {
  padding-top: 135px;
  display: flex;
  justify-content: space-between;

  &__item {
    position: relative;
    max-width: 512px;
    height: 400px;
    border: 2px solid #E4E4E4;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
    width: 100%;
    transition: all .3s ease-in-out;

    &:hover {
      border: 1px solid transparent;
      box-shadow: 0 8px 16px #C8C8C8;
    }

    .hot-icon {
      position: absolute;
      left: 0;
      top: 0;
    }

    .green-icon {
      position: absolute;
      right: -25px;
      top: -25px;
    }

    .blue-icon {
      position: absolute;
      right: -25px;
      top: -25px;
    }

    .fire-icon {
      position: absolute;
      left: 28px;
      top: 28px;
    }

    &::after {
      position: absolute;
      bottom: 110px;
      left: 16px;
      content: '';
      width: 480px;
      height: 2px;
      background-color: #F0F0F0;
    }

    &-img {
      margin: -112px auto 24px;
      width: 320px;
      height: 320px;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      border-radius: 50%;
      border: 4px solid #FFFFFF;
      background-color: #F0F0F0;
    }

    &-title {
      text-align: center;
      font-weight: 500;
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 60px;
    }

    &-info {
      display: flex;
      justify-content: space-around;
      font-size: 18px;
      line-height: 27px;
    }
  }
}

.upcoming {
  padding-top: 45px;

  &__title {
    font-size: 48px;
    line-height: 72px;
    font-weight: 400;
    margin-bottom: 18px;
  }

  &__item {
    padding: 22px 145px 22px 22px;
    border: 2px solid #E4E4E4;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
    width: 100%;
    display: flex;
    margin-bottom: 8px;

    &-images {
      display: flex;
    }

    &-social {
      margin-bottom: 16px;
      width: 72px;
      display: flex;
      justify-content: space-between;
    }

    &-text {
      width: 100%;
      padding-left: 56px;
      display: flex;
      justify-content: space-between;
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &-crypto {
        font-weight: 500;
        font-size: 36px;
        line-height: 54px;
      }

      &-total {
        font-size: 18px;
        line-height: 27px;
        color: #404040;
      }

      &-date {
        font-size: 18px;
        line-height: 27px;
        color: #404040;
      }
    }

    &-title {
      font-weight: 500;
      font-size: 36px;
      line-height: 54px;
    }

    &-description {
      font-size: 18px;
      line-height: 27px;
      margin-bottom: 25px;
    }

    &-link {
      font-size: 18px;
      line-height: 27px;
      text-decoration-line: underline;
      color: #0090F8;
    }

    &-images-item {
      width: 192px;
      height: 192px;
      background-color: #F0F0F0;
      margin-right: 8px;
      -webkit-border-radius: 2px;
      -moz-border-radius: 2px;
      border-radius: 2px;
    }
  }
}

.new-collection {
  margin-top: 50px;
  padding-bottom: 120px;
  padding-top: 65px;
  background-image: url(../public/images/collection-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;

  &__poster {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    max-width: 200px;
    font-family: 'Big Shoulders Display', sans-serif;
    font-weight: 700;
    font-size: 96px;
    line-height: 128%;
    color: #FFFFFF;
    margin-bottom: 35px;
  }

  &__description {
    max-width: 380px;
    font-size: 24px;
    line-height: 160%;
    color: #FFFFFF;
    margin-bottom: 60px;
  }

  &__date {
    font-family: 'Big Shoulders Display', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 96px;
    line-height: 128%;
    color: #FFFFFF;
  }

  &__price {
    font-family: 'Big Shoulders Display', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 96px;
    line-height: 128%;
    color: #FFFFFF;
  }

  &__buttons {
    display: flex;
  }

  .btn-mint {
    width: 192px;
    height: 72px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-border-radius: 24px;
    -moz-border-radius: 24px;
    border-radius: 24px;
    border: none;
    background: linear-gradient(#48E8D8, #104858);
    font-weight: bold;
    font-size: 24px;
    line-height: 144%;
    color: #FFFFFF;
    margin-right: 24px;
  }

  .btn-more {
    width: 192px;
    height: 72px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-border-radius: 24px;
    -moz-border-radius: 24px;
    border-radius: 24px;
    border: 2px solid #FFFFFF;
    background: transparent;
    font-size: 24px;
    line-height: 144%;
    color: #FFFFFF;
  }
}

.sales {
  padding-top: 50px;
  padding-bottom: 80px;

  &__title {
    font-size: 48px;
    line-height: 72px;
    font-weight: 400;
    margin-bottom: 30px;
  }
}

.promo {
  background-image: url("../public/images/promo-bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  height: 240px;

  &__title {
    text-align: center;
    font-weight: 200;
    font-size: 144px;
    line-height: 216px;
    letter-spacing: 150px;
    color: #FFFFFF;
    text-indent: 1em;
  }
}

.footer {

  &__top {
    background-color: #100418;
    color: #FFFFFF;
    font-size: 18px;
    line-height: 192%;
    padding: 64px 0;

    &-text1 {
      margin-bottom: 35px;
    }
  }

  &__down {
    background-color: #000000;
    padding: 50px 0 36px 255px;

    &-content {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-right {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
    }
  }

  &__menu-list {
    display: flex;

    &-item {
      margin-right: 15px;
    }

    &-link {
      font-size: 18px;
      line-height: 27px;
      color: #FFFFFF;
      text-decoration: none;
      transition: all .2s ease-in-out;
      padding-bottom: 1px;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__social {
    display: flex;
    padding-right: 8px;

    &-item {
      margin-right: 16px;
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: nowrap;
  }

  .btn-promo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 160px;
    height: 64px;
    font-weight: bold;
    font-size: 18px;
    line-height: 144%;
    color: #FFFFFF;
    border: none;
    background-color: #9850D0;
    -webkit-border-radius: 16px;
    -moz-border-radius: 16px;
    border-radius: 16px;
    margin-right: 24px;

  }
}